import React, { useState, useEffect } from "react";
import { useParams, Link, useNavigate } from "react-router-dom";
import Grid from "@mui/material/Grid";
import SaveIcon from "@mui/icons-material/Save";
import axios from "axios";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import CloudUploadIcon from "@mui/icons-material/CloudUpload";
import ClearIcon from "@mui/icons-material/Clear";
import Title from "../components/Title";
import LoadingButton from "@mui/lab/LoadingButton";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogActions from "@mui/material/DialogActions";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";

export default function NewEvent({ user }) {
  const params = useParams();
  const eventID = params.eventID;
  const navigate = useNavigate();
  const [eventData, setEventData] = useState();
  const [checked, setChecked] = React.useState([1]);
  const [eventAnnouncementPhoto, setEventAnnouncementPhoto] = useState();
  const [eventCelebrationPhoto, setEventCelebrationPhoto] = useState();
  const [selectedVideo, setSelectedVideo] = useState();
  const [name, setName] = useState();
  const [targetAmount, setTargetAmount] = useState();
  const [requestLoading, setLoading] = useState(false);
  const [openDialog, setOpenDialog] = useState(false);
  const [missingPhoto, setMissingPhoto] = useState(true);
  const [missingName, setMissingName] = useState(true);
  const [missingAmount, setMissingAmount] = useState(true);
  const [exceedsFileSize, setExceedsFileSize] = useState(false);

  const handleSave = async () => {
    const formData = new FormData();

    if (!eventAnnouncementPhoto) {
      setMissingPhoto(true);
    } else {
      setMissingPhoto(false);
    }

    if (!name) {
      setMissingName(true);
    } else {
      setMissingName(false);
    }

    if (!targetAmount) {
      setMissingAmount(true);
    } else {
      setMissingAmount(false);
    }
    if (eventAnnouncementPhoto && name && targetAmount) {
      try {
        formData.append("username", "admin");
        formData.append("password", "aDMin1234");
        formData.append("event_name", name);
        formData.append("image", eventAnnouncementPhoto);
        formData.append("target_amount", targetAmount);

        // You can handle the API request here
        const response = await axios.post(
          "https://smart-donate.com/backend/api/addEvent.php",
          formData
        );

        // Handle success
        // console.log("Event data updated successfully.");
        setLoading(false);
        navigate("/events");
        window.location.reload();
      } catch (error) {
        // Handle failure
        console.error("Event data update failed.");
      }
    } else {
      setOpenDialog(true);
    }
  };
  const handleCloseDialog = () => {
    setOpenDialog(false);
    setTimeout(() => {
      setExceedsFileSize(false);
    }, 1000);
  };
  function handleFileChange(e) {
    e.preventDefault();

    const selectedFile = e.target.files[0];
    console.log(selectedFile.size, "size");

    if (selectedFile) {
      // Check if the selected file size is within the limit (2MB in bytes)
      const maxSize = 2 * 1024 * 1024; // 2MB
      if (selectedFile.size <= maxSize) {
        // File size is within the limit
        setEventAnnouncementPhoto(selectedFile);
      } else {
        // File size exceeds the limit
        setExceedsFileSize(true);
        setOpenDialog(true);

        // Clear the input value to allow selecting a different file
        e.target.value = "";
      }
    }
  }

  const handleCelebrationFileChange = (event) => {
    const file = event.target.files[0];
    setEventCelebrationPhoto(file);
  };

  const handleVideoChange = (event) => {
    const file = event.target.files[0];
    setSelectedVideo(file);
  };

  const handleClear = () => {
    setEventAnnouncementPhoto(null);
  };
  const handleCelebrationPhotoClear = () => {
    setEventCelebrationPhoto(null);
  };
  const handleVideoClear = () => {
    setSelectedVideo(null);
  };
  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };
  useEffect(() => {
    // Define the URL with query parameters
    const apiUrl = "https://smart-donate.com/backend/api/getEvents.php";

    // Define the query parameters as an object
    const params = {
      username: "admin",
      password: "aDMin1234",
      event_id: eventID,
    };

    // Make a GET request using Axios
    axios
      .get(apiUrl, { params })
      .then((response) => {
        // Handle the API response here
        // console.log("MAY DAAAATA:", response.data);
        setEventData(response.data);
      })
      .catch((error) => {
        // Handle any errors here
        console.error("Error:", error);
      });
  }, [eventID]);
  return (
    <>
      <Title pageTitle={"إضافة فعالية"} />
      <div style={{ display: "flex", flexDirection: "column" }}>
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "flex-start",
          }}
        >
          <LoadingButton
            disableElevation
            size="small"
            onClick={handleSave}
            variant="contained"
            style={{
              color: "white",
              textAlign: "center",

              display: "flex",
              flexDirection: "row",
              //justifyContent: "flex-end",
              margin: "5px 5px 5px 140px",
              width: "100px",
              height: "30px",
              background: "linear-gradient(45deg, #BDD99E, #01A66F)",
            }}
            disabled={requestLoading}
            loading={requestLoading}
            // onClick={handleSave}
            endIcon={<SaveIcon sx={{ justifySelf: "right" }} />}
          >
            حفظ
          </LoadingButton>
          {/* <Button
            size="small"
            variant="contained"
            style={{
              color: "white",
              display: "flex",
              flexDirection: "row",
              justifyContent: "flex-end",
              margin: "5px",
              width: "100px",
              height: "30px",
              background: "linear-gradient(45deg, #BDD99E, #01A66F)",
            }}
            endIcon={<AddIcon />}
            onClick={() => {}}
          >
            إضافة جهاز
          </Button> */}
          <Button
            component={Link}
            to="/events"
            size="small"
            variant="outlined"
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "flex-end",
              marginLeft: "140px",
              margin: "5px",
              height: "30px",
              color: "#BDD99E",
              borderColor: "#BDD99E",
              width: "50px",
            }}
            endIcon={<ClearIcon style={{ scale: "1.2" }} />}
          >
            إلغاء{" "}
          </Button>
        </div>
        <Grid
          container
          spacing={2}
          height="650px"
          sx={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "flex-direction",
            width: "100%",
          }}
        >
          <Grid item xs={7} md={7} spacing={2}>
            <Grid
              container
              width="100%"
              height="100%"
              sx={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "space-between",
              }}
            >
              <Grid
                spacing={2}
                sx={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "flex-end",
                  margin: "0",
                  padding: "0",
                }}
              >
                {" "}
                <>
                  {/* {" "}
          <CardMedia
            component="img"
            width="115%"
            image={location.state?.img || eventData.image}
            sx={{ borderRadius: "30px", height: "533px" }}
          /> */}

                  <div
                    Grid
                    item
                    draggable
                    onDragOver={(e) => e.preventDefault()}
                    onDrop={(e) => {
                      e.preventDefault();
                      const maxSize = 2 * 1024 * 1024; // 2MB
                      const droppedFile = e.dataTransfer.files[0];
                      console.log(e.dataTransfer.files[0].size, "size");
                      if (e.dataTransfer.files[0].size <= maxSize) {
                        setEventAnnouncementPhoto(droppedFile);
                      } else {
                        setExceedsFileSize(true);
                        setOpenDialog(true);
                      }
                    }}
                    style={{
                      border: "2px dotted #D8D8D8",
                      borderRadius: "30px",
                      padding: "16px",
                      textAlign: "center",
                      marginTop: "2px",
                      // width: "280px",
                      height: "500px",
                    }}
                  >
                    <input
                      type="file"
                      name="image"
                      accept="image/*"
                      style={{ display: "none" }}
                      onChange={handleFileChange}
                    />
                    {eventAnnouncementPhoto ? (
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "column",
                          justifyContent: "center",
                          alignItems: "center",
                          // width: "280px",
                          height: "500px",
                        }}
                      >
                        <ClearIcon
                          onClick={handleClear}
                          // variant="contained"
                          sx={{
                            color: "#989898",
                            ":hover": {
                              color: "#C8C8C8",
                            },
                          }}
                          alt="حذف"
                        />
                        <p>Selected file: {eventAnnouncementPhoto.name}</p>
                        <img
                          src={URL.createObjectURL(eventAnnouncementPhoto)}
                          height="300px"
                          width="auto"
                          alt={eventAnnouncementPhoto.name}
                        />
                      </div>
                    ) : (
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "column",
                          justifyContent: "center",
                          alignItems: "center",
                          // width: "280px",
                          height: "500px",
                        }}
                      >
                        <p style={{ color: "#989898" }}>
                          ... قم بإدراج صورة إعلان الفعالية أو
                        </p>

                        <Button
                          component="label"
                          variant="contained"
                          startIcon={<CloudUploadIcon />}
                          dir="ltr"
                          sx={{
                            background:
                              "linear-gradient(45deg, #BDD99E, #01A66F)",
                          }}
                        >
                          تحميل صورة
                          <input
                            type="file"
                            accept="image/*"
                            style={{ display: "none" }}
                            onChange={handleFileChange}
                          />
                        </Button>

                        <p
                          style={{
                            color: "#989898",
                            fontSize: "12px",
                            marginTop: "0",
                          }}
                        >
                          أقصى حجم مسموح به: ٢ ميجابايت
                        </p>
                      </div>
                    )}
                  </div>
                </>
              </Grid>
            </Grid>
          </Grid>
          <Grid
            item
            xs={3}
            md={3}
            sx={{
              display: "flex",
              height: "100%",
              flexDirection: "column",
              justifyContent: "flex-start",
            }}
          >
            {/* <Grid item>
              <div
                style={{
                  height: "100%",
                  background: "black",
                  borderRadius: "30px",
                }}
              >
                <AddMaterialTable
                  sx={{ height: "100%" }} // Add this style to make the table take 100% height
                />
              </div>
            </Grid> */}
            <TextField
              // Grid
              // item
              id="outlined-required"
              placeholder=" اسم الفعالية"
              dir="rtl"
              value={name}
              width="100%"
              height="100%"
              multiline
              rows={2}
              sx={{ borderRadius: "30px", marginBottom: "20px" }}
              onChange={(event) => setName(event.target.value)}
            />
            <TextField
              type="text"
              style={{
                borderRadius: "30px",
              }}
              dir="rtl"
              multiline
              rows={5}
              value={targetAmount}
              onChange={(event) => {
                // Replace non-English digits with their English equivalents
                const input = event.target.value;
                const englishNumbers = input.replace(
                  /[٠١٢٣٤٥٦٧٨٩]/g,
                  (match) => {
                    const arabicNumbers = "٠١٢٣٤٥٦٧٨٩";
                    const englishNumbers = "0123456789";
                    return englishNumbers.charAt(arabicNumbers.indexOf(match));
                  }
                );
                // Check for non-numeric characters and filter them out
                const numericInput = englishNumbers.replace(/[^0-9]/g, "");
                setTargetAmount(numericInput);
              }}
              inputProps={{
                inputMode: "numeric",
              }}
              placeholder="المبلغ المطلوب"
            ></TextField>
          </Grid>
        </Grid>
      </div>
      <Dialog open={openDialog} onClose={handleCloseDialog} dir="rtl">
        <DialogContent>
          {missingPhoto && !exceedsFileSize && (
            <p>
              <ErrorOutlineIcon sx={{ color: "red" }} /> يرجى اختيار صورة.
            </p>
          )}
          {missingName && !exceedsFileSize && (
            <p>
              <ErrorOutlineIcon sx={{ color: "red" }} /> يرجى إدخال اسم
              الفعالية.
            </p>
          )}
          {missingAmount && !exceedsFileSize && (
            <p>
              <ErrorOutlineIcon sx={{ color: "red" }} /> يرجى إدخال المبلغ
              المطلوب.
            </p>
          )}
          {exceedsFileSize && (
            <p>
              <ErrorOutlineIcon sx={{ color: "red" }} /> حجم الصورة أكبر من ٢
              ميجابايت
            </p>
          )}
        </DialogContent>
        <DialogActions>
          <Button
            variant="outlined"
            onClick={() => handleCloseDialog()}
            sx={{
              background: "linear-gradient(45deg, #BDD99E, #01A66F)",
              color: "#ffff",
              width: "100px",
              height: "30px",
              border: "none",
              ":hover": {
                border: "none",
              },
            }}
            disabled={requestLoading}
          >
            موافق
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
